












import { Component, Vue, Watch, Prop } from 'vue-property-decorator';

@Component({
  components: {

  }
})

export default class Breadcrumb extends Vue {
  @Prop({
    type: Array,
    default(): any {
      return [];
    }
  })
  private list: any;

  private goUrl(name: string, type: number): void {
    if (!name) return;
    if (type === 2) {
      this.$router.push({
        path: name
      });
    } else {
      this.$router.push({
        name
      });
    }
  }
}
